import { Component, OnInit, SimpleChanges } from '@angular/core';
import { ResourceService } from 'src/app/config/resource.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
    passcode = void 0;
    httpError = void 0;
    bookmarkName = '';
    bookmarkUrl = '';
    bookmarks = void 0;
    todos = void 0
    original = void 0;
    cheGist = 'https://api.github.com/gists/973de2c3eb3f55518e97a1d3d18eb79e';
    gistFile = 'bookmarks.json';
    gistTodo = 'todos.json';
    confirmRowNumber = Number.MAX_SAFE_INTEGER;
    confirmTodoRowNumber = Number.MAX_SAFE_INTEGER;
    
    constructor(private resourceService: ResourceService) { }

    ngOnInit() { 
        
    }

    loadGist () {
        this.httpError = void 0;
        this.resourceService.getGist(this.cheGist, this.passcode).subscribe(
            result => {
                this.bookmarks = JSON.parse(result[this.gistFile]['content']).data;
                this.original = this.deepClone(this.bookmarks);
                this.todos = JSON.parse(result[this.gistTodo]['content']).data;
            },
            error => {
                this.httpError = error.statusText;
            }
        );
    }

    addToGist () {
        this.httpError = void 0;
        const newEntry = {
            name: this.bookmarkName,
            url: this.bookmarkUrl
        }
        this.bookmarks.push(newEntry);
        this.resourceService.patchGist(this.gistFile, this.cheGist, this.bookmarks, this.passcode).subscribe(
            (val) => {
                console.log("PATCH call successful value returned in body", val);
            },
            error => {
                this.httpError = error.statusText;
            },
            () => {
                console.log("The PATCH observable is now completed.");
        });
        this.bookmarkName = '';
        this.bookmarkUrl = '';
    }

    addTodo (event) {
        if (event.target.value) {
            this.httpError = void 0;
            const newEntry = {
                content: event.target.value
            }
            this.todos.push(newEntry);
            this.resourceService.patchGist(this.gistTodo, this.cheGist, this.todos, this.passcode).subscribe(
                (val) => {
                    console.log("PATCH call successful value returned in body", val);
                },
                error => {
                    this.httpError = error.statusText;
                },
                () => {
                    console.log("The PATCH observable is now completed.");
            });
            event.target.value = '';
        }
    }

    deleteFromGist (url, index) {
        if(this.confirmRowNumber === index){
            this.httpError = void 0;
            this.bookmarks = this.bookmarks.filter(bookmark => bookmark.url !== url);
            this.resourceService.patchGist(this.gistFile, this.cheGist, this.bookmarks, this.passcode).subscribe(
                (val) => {
                    console.log("PATCH call successful value returned in body", val);
                },
                error => {
                    this.httpError = error.statusText;
                },
                () => {
                    console.log("The PATCH observable is now completed.");
            });
            this.confirmRowNumber = Number.MAX_SAFE_INTEGER;
        }
        this.confirmRowNumber = index;
    }

    cancelRowDeletion () {
        this.confirmRowNumber = Number.MAX_SAFE_INTEGER;
    }

    deleteTodoFromGist (content, index) {
        if(this.confirmTodoRowNumber === index){
            this.httpError = void 0;
            this.todos = this.todos.filter(todo => todo.content !== content);
            this.resourceService.patchGist(this.gistTodo, this.cheGist, this.todos, this.passcode).subscribe(
                (val) => {
                    console.log("PATCH call successful value returned in body", val);
                },
                error => {
                    this.httpError = error.statusText;
                },
                () => {
                    console.log("The PATCH observable is now completed.");
            });
            this.confirmTodoRowNumber = Number.MAX_SAFE_INTEGER;
        }
        this.confirmTodoRowNumber = index;
    }

    setKeypass (keypass: string) {
        this.passcode = keypass;
        this.loadGist();
    }

    clearPasscode () {
        this.passcode = void 0;
        this.httpError = void 0;
    }

    search (event) {
        // event.target.value
        if (event.target.value) {
            this.bookmarks = this.bookmarks.filter(bookmark => bookmark.name.toLowerCase().includes(event.target.value.toLowerCase()));
            event.target.value = '';
        } else {
            this.bookmarks = this.deepClone(this.original);
        }
    }

    deepClone (obj) {
        return JSON.parse(JSON.stringify(obj));
    }
}
