import { Component, OnInit, AfterViewInit, ViewChild, ElementRef  } from '@angular/core';

declare var videojs: any;

@Component({
  selector: 'app-mediaplayer',
  templateUrl: './mediaplayer.component.html',
  styleUrls: ['./mediaplayer.component.scss']
})
export class MediaplayerComponent implements OnInit {
  // Instancia do video.js.
  vidObj: any;
  // Poster para ser usado no video.js
  poster: string;
  // URL do video a ser reproduzido.
  video: string;
  // Acessa o elemento video do html5 via viewchild.
  @ViewChild('myvid') vid: ElementRef;

  constructor() {
    this.poster = 'assets/img/movie-icon.png';
    this.video = void 0;
  }

  ngOnInit() {
  }

  getMedia() {
    const options = {
      controls: true,
      autoplay: false,
      preload: 'auto',
      techOrder: ['html5']
    };

    // this.vidObj = new videojs(this.vid.nativeElement, options, function onPlayerReady() {
    //   videojs.log('Your player is ready!');
    // });
    const player = videojs('my-video', options);
    player.pause();
    player.src(this.video);
    // set src track corresponding to new movie //
    player.load();
  }

  ngAfterViewInit() {
    
  }
}
