import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { SimplemdeModule } from 'ngx-simplemde';

import { AppComponent } from './app.component';
import { MediaplayerComponent } from './components/mediaplayer/mediaplayer.component';
import { EncryptionComponent } from './components/encryption/encryption.component';
import { JsonfetchComponent } from './components/jsonfetch/jsonfetch.component';
import { HomeComponent } from './components/home/home.component';
import { NotesComponent } from './components/notes/notes.component';

const appRoutes: Routes = [
  { path: 'media-player', component: MediaplayerComponent },
  { path: 'json-fetcher', component: JsonfetchComponent },
  { path: 'encryption', component: EncryptionComponent },
  { path: 'notes', component: NotesComponent },
  { path: '', component: HomeComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    MediaplayerComponent,
    EncryptionComponent,
    JsonfetchComponent,
    HomeComponent,
    NotesComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    SimplemdeModule.forRoot(),
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
