import { Component, OnInit } from '@angular/core';
import { ResourceService } from 'src/app/config/resource.service';

@Component({
  selector: 'app-notes',
  templateUrl: './notes.component.html',
  styleUrls: ['./notes.component.scss']
})
export class NotesComponent implements OnInit {
  notes;
  original;
  editingNoteContent = '';
  editingNoteNumber = void 0;
  cheGist = 'https://api.github.com/gists/973de2c3eb3f55518e97a1d3d18eb79e';
  gistFile = 'notes.json';
  passcode = void 0;
  httpError = '';

  constructor(private resourceService: ResourceService) { }

  ngOnInit() { }

  generateId () {
    return Math.floor(Math.random() * Number.MAX_SAFE_INTEGER);
  }

  getTitle (content: string) {
    return content.slice(0, 35);
  }

  editNote (id: number) {
    const note = this.notes.filter(note => note.id === id);
    this.editingNoteNumber = id;
    this.editingNoteContent = note[0].content;
  }

  cancelUpdateNote () {
    this.editingNoteContent = '';
    this.editingNoteNumber = void 0;
  }

  loadNotes () {
    this.httpError = void 0;
    this.resourceService.getGist(this.cheGist, this.passcode).subscribe(
        result => {
            this.notes = JSON.parse(result[this.gistFile]['content']).data;
            this.original = this.deepClone(this.notes);
        },
        error => {
            this.httpError = error.statusText;
        }
    );
  }

  addNote () {
    this.httpError = void 0;
    const newEntry = {
      id: this.generateId(),
      content: this.editingNoteContent
    }
    this.notes.push(newEntry);
    this.resourceService.patchGist(this.gistFile, this.cheGist, this.notes, this.passcode).subscribe(
      (val) => {
        console.log("PATCH call successful value returned in body", val);
      },
      error => {
        this.httpError = error.statusText;
      },
      () => {
        console.log("The PATCH observable is now completed.");
    });
    this.editingNoteContent = ''; 
  }

  updateNote () {
    this.httpError = void 0;
    if (this.editingNoteNumber) {
      this.notes.map(note => 
        note.id === this.editingNoteNumber ? note.content = this.editingNoteContent : note
      );
      this.resourceService.patchGist(this.gistFile, this.cheGist, this.notes, this.passcode).subscribe(
        (val) => {
          console.log("PATCH call successful value returned in body", val);
        },
        error => {
          this.httpError = error.statusText;
        },
        () => {
          console.log("The PATCH observable is now completed.");
        });
    }
  }

  deepClone (obj) {
    return JSON.parse(JSON.stringify(obj));
  }

  setKeypass (keypass: string) {
    this.passcode = keypass;
    this.loadNotes();
  }

  clearPasscode () {
    this.passcode = void 0;
    this.httpError = void 0;
  }

  search (event) {
    // event.target.value
    if (event.target.value) {
        this.notes = this.notes.filter(note => note.content.toLowerCase().includes(event.target.value.toLowerCase()));
        event.target.value = '';
    } else {
        this.notes = this.deepClone(this.original);
    }
  }
}
